
#inackodering .orange{
    background-color: #ffd3a4;
}
#inackodering .blue{
    background-color: #f0f8ff;
}
#inackodering .box{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 10px 20px;
    border: 1px solid #452a1b;
}
#inackodering h4{
    margin: 0;
}
#inackodering ul{
    list-style-type: circle;
    margin: 0;
}
#inackodering li{
    padding: 10px 0;
    position: relative;
    right: 20px;
}



